import { Component, OnInit, Input } from '@angular/core';
import { UserService } from 'src/app/services/user.service';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { AuthPending } from 'src/app/store/actions/auth.actions';
import { MatDialogRef, MatDialog } from '@angular/material';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public userLogin: string;
  public userPassword: string;
  public loginResult: string;
  public dialogRef: MatDialogRef<LoginComponent>;
  public dialog: MatDialog;

  private userService: UserService;
  private router: Router;
  private store: Store<any>;

  constructor(
    userService: UserService,
    router: Router,
    store: Store<any>,
    dialogRef: MatDialogRef<LoginComponent>,
    dialog: MatDialog) {
    this.userService = userService;
    this.router = router;
    this.store = store;
    this.dialogRef = dialogRef;
    this.dialog = dialog;
  }

  ngOnInit() {
    this.userLogin = null;
    this.userPassword = null;
  }



  onNoClick(): void {
    this.dialogRef.close();
  }

  loginFunction() {
    var user: User = { login: this.userLogin, password: this.userPassword } as User;
    this.store.dispatch(new AuthPending(user));
    this.store.select('user').subscribe(
      (data: User) => { if (data.userId > 0) { this.success(data); this.dialogRef.close(); } else { this.fail() } },
      (error) => { this.fail(); });
  }

  success(data: User) {
    this.loginResult = null;
    this.router.navigate(['/admin/dish']);
  }

  fail() {
    this.loginResult = "User " + this.userLogin + " does not exists!";
  }
}
