import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { Observable, of } from 'rxjs';
import { Action } from '@ngrx/store';
import { AuthActionTypes, AuthPending, AuthError, AuthSuccess } from '../actions/auth.actions';
import { catchError, map, mergeMap, switchMap } from 'rxjs/operators';
import { UserService } from 'src/app/services/user.service';



@Injectable()
export class AuthEffects {

  private userService: UserService;

  constructor(private actions$: Actions,
    userService: UserService) {
    this.userService = userService;
  }

  @Effect()
  user$: Observable<Action> = this.actions$.pipe(
    ofType(AuthActionTypes.Pending),
      switchMap((action: AuthPending) => {
        return this.userService.login(action.payload).pipe(
          // If successful, dispatch success action with result
          map((res: User) => new AuthSuccess(res)),
          // If request fails, dispatch failed action
          catchError((err) => of(new AuthError(err)))
        );
      })
  )
}
