import { Action } from '@ngrx/store';

export enum AuthActionTypes {
  Pending = 'Pending',
  Update = 'Pending',
  Success = 'Success',
  Error = 'Error' 
}

export class AuthPending implements Action {
  
  public readonly type = AuthActionTypes.Pending;
  public payload: User;

  constructor(payload: User){
    this.payload = payload;
  }
}

export class AuthUpdate implements Action {
  
  public readonly type = AuthActionTypes.Pending;
  public payload: User;

  constructor(payload: User){
    this.payload = payload;
  }
}

export class AuthSuccess implements Action {
  public readonly type = AuthActionTypes.Success;
  public payload: User;

  public constructor(
    payload: User
  ) {
    this.payload = payload;
   }
}

export class AuthError implements Action {
  public readonly type = AuthActionTypes.Error;
  public payload: any;

  public constructor(
    payload: any
  ) {
    this.payload = payload;
   }
}