import { Component, OnInit, Sanitizer, ViewChild } from '@angular/core';
import { DishService } from 'src/app/services/dish.service';
import { MatTableDataSource, MatIconRegistry, MatPaginator, MatSort } from '@angular/material';
import { DishTypesService } from 'src/app/services/dishtypes.service';
import { DomSanitizer } from '@angular/platform-browser';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { UploadService } from 'src/app/services/upload.service';

@Component({
  selector: 'app-dish',
  templateUrl: './dish.component.html',
  styleUrls: ['./dish.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0px', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class DishComponent implements OnInit {

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  private dishService: DishService;
  private dishTypesService: DishTypesService;
  private dishes: Dish[];
  private user: User;
  private store: Store<any>;
  private uploadService: UploadService;
  private imageFile: File;

  public dataSource: MatTableDataSource<Dish>;
  public dish: Dish;
  public dishTypes: DishType[];
  public dishType: DishType;
  public displayedColumns: string[] = ['type', 'name', 'description', 'image', 'price', 'buttons'];
  public expandedElement: Dish | null;
  public selectedDishTypeId: FormControl;
  public router: Router;
  public dishImage: DishImage;

  constructor(
    dishService: DishService,
    dishTypesService: DishTypesService,
    router: Router,
    store: Store<any>,
    uploadService: UploadService) {
    this.dishService = dishService;
    this.dishTypesService = dishTypesService;
    this.dish = {} as Dish;
    this.dishType = {} as DishType;
    this.dishTypes = [] as DishType[];
    this.dishImage = {} as DishImage;
    this.router = router;
    this.store = store;
    this.uploadService = uploadService;
  }

  ngOnInit() {
    this.updateTable();
    this.dishTypesService.getAllDishTypes().subscribe(s => this.dishTypes = s);
    this.selectedDishTypeId = new FormControl(2);
    this.store.select('user').subscribe(s => this.user = s);
  }

  postDish() {
    this.dish.dishTypeId = +this.dishType.dishTypeId;
    this.dish.dishId = 0;
    this.dish.userId = this.user.userId;
    this.dish.dishImageId = this.dishImage.dishImageId;
    this.dishService.postDish(this.dish).subscribe(s => this.updateTable());
  }

  updateDish(dish: Dish) {
    this.dish.dishTypeId = this.dishType.dishTypeId
      ? this.dishType.dishTypeId
      : dish.dishTypeId;
    this.dish.dishId = +dish.dishId;
    this.dish.userId = this.user.userId;
    this.dish.name = this.dish.name 
    ? this.dish.name 
    : dish.name;
    this.dish.description = this.dish.description 
    ? this.dish.description 
    : dish.description;
    this.dish.dishImageId = this.dishImage.dishImageId
    ? this.dishImage.dishImageId 
    : dish.dishImageId;;
    this.dish.price = this.dish.price 
    ? this.dish.price 
    : dish.price;
    this.dishService.putDish(this.dish).subscribe(s => this.updateTable());
  }

  deleteDish(id: number) {
    this.dish.dishTypeId = +this.dishType.dishTypeId;
    this.dishService.removeDish(id).subscribe(s => this.updateTable());
  }

  updateTable() {
    this.dishImage = {} as DishImage;
    this.dishService.getAllDishes().subscribe(s => {
      this.dishes = s;
      this.dataSource = new MatTableDataSource(this.dishes);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      console.log(s);
    });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  uploadFile(input) {
    this.imageFile = input.files[0];
    this.uploadService.uploadDishImage(this.imageFile, this.dishImage);
  }

  changeCurrentDishType(dishTypeId: number) {
    this.dishType.dishTypeId = dishTypeId;
  }
}
