import { Action } from '@ngrx/store';

export enum BasketActionTypes {
  Add = 'Add',
  Decrement = 'Decrement',
  Remove = 'Remove',
  Error = 'Error'
}

const initialState: BasketItem[] = JSON.parse(localStorage.getItem('basket')) || [];

export class BasketAdd implements Action {

  public readonly type = BasketActionTypes.Add;
  public payload: BasketItem[];

  constructor(dish: Dish) {
    var elem = initialState.find(f => f.dish.dishId === dish.dishId);
    elem
      ? elem.count += 1
      : initialState.push({ dish: dish, count: 1 } as BasketItem);
    this.payload = initialState;
  }
}

export class BasketDecrement implements Action {

  public readonly type = BasketActionTypes.Decrement;
  public payload: BasketItem[];

  constructor(dish: Dish) {
    initialState.find(f => f.dish.dishId === dish.dishId).count -= 1;
    this.payload = initialState;
  }
}

export class BasketRemove implements Action {

  public readonly type = BasketActionTypes.Remove;
  public payload: BasketItem[];

  constructor(dish: Dish) {
    initialState.splice(initialState.findIndex(f => f.dish.dishId === dish.dishId), 1);
    this.payload = initialState;
  }
}

export class BasketError implements Action {
  public readonly type = BasketActionTypes.Error;
  public payload: any;

  public constructor(
    payload: any
  ) {
    this.payload = payload;
  }
}