import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DishService {

  private http: HttpClient;

  constructor(http: HttpClient) {
    this.http = http;
  }

  getAllDishes() {
    return this.http.get<Dish[]>('https://vladimirsuhov.space/api/dishes');
  }

  getDishesByType(id: number) {
    return this.http.get<Dish[]>('https://vladimirsuhov.space/api/dishes/bytype/'+id);
  }

  postDish(dish: Dish) {
    return this.http.post<Dish>('https://vladimirsuhov.space/api/dishes/', dish);
  }

  putDish(dish: Dish) {
    return this.http.post<Dish>('https://vladimirsuhov.space/api/dishes/update/' + dish.dishId, dish);
  }

  removeDish(id: number) {
    return this.http.get<Dish>('https://vladimirsuhov.space/api/dishes/delete/' + id);
  }
}
