import { Action } from '@ngrx/store';
import { AuthActionTypes } from '../actions/auth.actions';
import { BasketActionTypes } from '../actions/backet.actions';


export interface State {

}

export const initialState: BasketItem[] = JSON.parse(localStorage.getItem('basket'));

export function basketReducer(state = initialState, action): State {
  switch (action.type) {
    case BasketActionTypes.Add:
      return action.payload;
    case BasketActionTypes.Remove:
      return action.payload;
    case BasketActionTypes.Decrement:
      return action.payload;
    default:
      return initialState;
  }
}
