import { Component, OnInit, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { DishService } from 'src/app/services/dish.service';
import { DishTypesService } from 'src/app/services/dishtypes.service';
import { Store } from '@ngrx/store';
import { BasketAdd, BasketRemove, BasketDecrement } from 'src/app/store/actions/backet.actions';
import { flatMap } from 'rxjs/operators';
import { MatDialog } from '@angular/material';
import { LoginComponent } from '../login/login.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  private router: Router;
  private dishService: DishService;
  private dishTypesService: DishTypesService;
  private store: Store<any>;
  private elementRef: ElementRef;
  private dialog: MatDialog;

  public dishes: Dish[];
  public dishTypes: DishType[];
  public dishType: DishType;
  public user: User;
  public basket: BasketItem[];
  public basketCount: number;

  constructor(router: Router,
    dishService: DishService,
    dishTypesService: DishTypesService,
    store: Store<any>,
    elementRef: ElementRef,
    dialog: MatDialog
  ) {
    this.router = router;
    this.dishService = dishService;
    this.dishTypesService = dishTypesService;
    this.store = store;
    this.user = {} as User;
    this.basket = [] as BasketItem[];
    this.dishTypes = [] as DishType[];
    this.elementRef = elementRef;
    this.dialog = dialog;
  }

  ngOnInit() {
    this.dishService.getAllDishes().subscribe(s => this.dishes = s);
    this.dishTypesService.getAllDishTypes().subscribe(t => this.dishTypes = t);
    this.store.select('basket').subscribe(s => s ? this.basket = s : this.basket = [] as BasketItem[]);
    this.store.select('user').subscribe(s => this.user = s);
  }

  openLoginDialog(): void {
    const dialogRef = this.dialog.open(LoginComponent, {
      width: '250px'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  getBasketCount() {
    var count = 0;
    for(var i=0;i<this.basket.length;i++){
      count +=this.basket[i].count;
    }
    return count;
  }

  getFinalBasketPrice() {
    var price = 0;
    for(var i=0;i<this.basket.length;i++){
      price +=this.basket[i].count*this.basket[i].dish.price;
    }
    return price;
  }

  incrementBasketDish(basketItem: BasketItem) {
    this.store.dispatch(new BasketAdd(basketItem.dish));
    return false;
  }

  decrementBasketDish(basketItem: BasketItem) {
    this.store.dispatch(new BasketDecrement(basketItem.dish));
    return false;
  }

  addToBasket(basket: Dish) {
    this.store.dispatch(new BasketAdd(basket));
  }

  removeFromBasket(basket: Dish) {
    this.store.dispatch(new BasketRemove(basket));
  }

  getDishesByType(id: number) {
    this.dishService.getDishesByType(id).subscribe(s => this.dishes = s);
    this.dishType = this.dishTypes.find(f => f.dishTypeId == id);
  }

}
