import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Store } from '@ngrx/store';
import { AuthUpdate } from '../store/actions/auth.actions';

@Injectable({
  providedIn: 'root'
})
export class UploadService {

  private httpClient: HttpClient;
  private user: User;

  public store: Store<any>;

  constructor(httpClient: HttpClient,
    store: Store<any>) {
    this.httpClient = httpClient;
    this.store = store;
  }

  public uploadLogo(file: File) {
    this.store.select('user').subscribe(s => this.user = s);
    var reader = new FileReader();
    reader.readAsDataURL(file);
    var self = this;
    reader.onload = function () {
      console.log(reader.result);
      self.loadImage.call(self, reader);
    };
    reader.onerror = function (error) {
      console.log('Error: ', error);
    };
  }

  public uploadDishImage(file: File, dishImage: DishImage) {
    this.store.select('user').subscribe(s => this.user = s);
    var reader = new FileReader();
    reader.readAsDataURL(file);
    var self = this;
    reader.onload = function () {
      console.log(reader.result);
      self.loadDishImage.call(self, reader, dishImage);
    };
    reader.onerror = function (error) {
      console.log('Error: ', error);
    };
  }

  public loadImage(reader: FileReader){
    this.user.logo.image = reader.result.toString();
    this.user.logo.name = "changed";
    this.store.dispatch(new AuthUpdate(this.user));
    this.httpClient.post("https://vladimirsuhov.space/api/Logos/update/" + this.user.logo.logoId, this.user.logo);
  }

  public loadDishImage(reader: FileReader, dishImage: DishImage){
    dishImage.image = reader.result.toString();
    this.httpClient.post("https://vladimirsuhov.space/api/DishImages/", dishImage)
    .subscribe((s:DishImage) => dishImage.dishImageId = s.dishImageId);
  }
}
